.messageSender {
  display: flex;
  margin-top: 30;
  flex-direction: column;
  border-radius: 15px;
  /* box for floating effect */
  box-shadow: 0px 5px 17px -7px rgba(0, 0, 0.75);
  width: 100%;
}

.messageSender_top {
  display: flex;
  border-bottom: 1px solid green;
  padding: 15px;
}

.messageSender_top > form {
  flex: 1;
  display: flex;
}

.messageSender_top > form > input {
  outline-width: 0;
  border: 1px solid green;
  padding: 5px 20px;
  margin: 0 10px;
  border-radius: 999px;
}

.messageSender_top > form > button {
  /* Hide buttone */
  display: none;
  outline-width: 0;
  border: 1px solid green;
  padding: 5px 20px;
  margin: 5px 10px;
  border-radius: 999px;
}

/* media (max-width: 1200px){
    .messageSender_top > form > button  {
        display: visible;
      }
} */

.messageSender_input {
  flex: 1;
}

.messageSender_option > h3 {
  color: green;
  font-size: medium;
  margin-left: 10px;
}

/* pushes them into a row */
.messageSender_bottom {
  display: flex;
  justify-content: space-evenly;
}

.messageSender_option {
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 5px;
}

.messageSender_option:hover {
  background-color: white;
  border-radius: 999px;
  cursor: pointer;
}
