.header {
  padding: 15px 20px;
  display: flex;
  justify-content: space-between;
  position: sticky;
  background-color: white;
  z-index: 100;
  top: 0;
  box-shadow: 0px 5px 8px -9px rgba(0, 0, 0, 75);
}

.header_left {
  display: flex;
  justify-content: space-evenly;
}
.header_input {
  display: flex;
  align-items: center;
  background-color: #024b2c;
  padding: 10px;
  margin-left: 10px;
  border-radius: 33px;
}

.header_left > img {
  height: 40px;
}

.header_input > input {
  border: none;
  background-color: white;
  outline-width: 0;
}

.header_center {
  display: flex;
  flex: 1;
  justify-content: center;
}

.header__option > .MuiSvgIcon-root {
  color: yellowgreen;
}

.header__option:hover > .MuiSvgIcon-root {
  color: green;
}

.header__option--active > .MuiSvgIcon-root {
  color: #024b2c;
}

.header__option--active {
  border-bottom: 4px solid green;
}

.header__option {
  display: flex;
  align-items: center;
  padding: 0 30px;
  cursor: pointer;
}

.header__option:hover {
  background-color: yellowgreen;
  border-radius: 10px;
  align-items: center;
  padding: 0 30px;
  border-bottom: none;
}

.header__info {
  display: flex;
  align-items: center;
}

.header__info > h4 {
  margin-left: 10px;
}

.header_right {
  display: flex;
}
