.login {
  display: grid;
  place-items: center;
  height: 100vh;
}

.login > div > img {
  object-fit: contain;
  height: 150px;
}

.login > div {
  display: flex;
  flex-direction: column;
}

.login > button {
  width: 300px;
  background-color: yellowgreen;
  color: green;
  font-weight: 150px;
}

.login > button:hover {
  border-radius: 999px;
  background-color: white;
  cursor: pointer;
}
